.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}




.header {
  background-color: #e90059;
  color: white;
  width: 100%;
  height: 100px;
  font-family: sans-serif;
  font-size: larger;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
